import { createEvent, attach, sample } from 'effector';
import {notificationsApiClient} from 'shared/api/roketo-client';
import {$notifications} from 'services/wallet';

export const markAllRead = createEvent();

const markAllReadFx = attach({
  source: $notifications,
  async effect(notifications) {
    if (notifications.length !== 0) {
      await notificationsApiClient.markAllRead();
    }
  },
});

sample({
  clock: markAllRead,
  target: markAllReadFx,
});

sample({
  clock: markAllRead,
  source: $notifications,
  fn: (notifications) => notifications.map(
    (notification) => !notification.isRead ? { ...notification, isRead: true } : notification
  ),
  target: $notifications,
});
